.s-hero {
    width: 100%;
    height: 78.6rem;
    background: url(../img/bg-hero.svg) no-repeat center center;
    padding-top: 17.5rem;
    .container {
        @include flex (row, flex-start, space-between);
    }
    .text {
        width: 100%;
        max-width: 68.4rem;
        h3 {
            font-weight: 600;
            line-height: 120%;
            color: #FFFFFF;
            opacity: 0.8;
            margin-bottom: 3.2rem;
        }
        h1 {
            color: #FFFFFF;
            margin-bottom: 3.9rem;
        }
        ul {
            width: 100%;
            max-width: 62rem;
            margin-top: 6.4rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            li{
                display: flex;
                align-items: center;
                .icon {
                    width: 4.8rem;
                    height: 4.8rem;
                    background: linear-gradient(180deg, rgba(54, 204, 227, 0.4) 0%, rgba(53, 202, 226, 0) 100%);
                    border-radius: 50%;
                    @include flex (row, center, center);
                    margin-right: 1.1rem;
                }
                span {
                    font-size: 1.8rem;
                    line-height: 1.8rem;
                    color: #FFFFFF;  
                }

            }
        }
    }
    .area-image {
        width: 100%;
        max-width: 70.6rem;
        margin-right: -17.4rem;
        h2 {
            margin-bottom: 6.7rem;
            text-align: center;
            color: #FFFFFF;
            opacity: 0.4;
            font-weight: 600;
        }
        .image {
            position: relative;
            top:35px;
            width: 100%;
            height: 54.1rem;
            img {
                position: absolute;
            }
            .card-front {
                top: 4.1rem;
                left: 0;
                z-index: 2;
            }
            .card-back {
                bottom: 4.6rem;
                right: 8.6rem;
                z-index: 1;
            }
            .circle {
                top: 2.8rem;
                right: 4.3rem;
            }
        }
    }
}

.s-card-neon {
    padding-top: 13.1rem;
    .container{
        @include flex (row, flex-start, space-between);
    }
    .left-area {
        width: 100%;
        max-width: 27.8rem;
        .ilustra-mockup {
            position: relative;
            width: 100%;
            max-width: 27.8rem;
            margin-bottom: 12.9rem;
            .circle {
                position: absolute;
                top: 87px;
                left: -214px;
                max-width: 377px;
                z-index: -1; 
            }
        }
        .text {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            .icon {
                position: relative;
                &:before {
                    content: "";
                    width: 5px;
                    height: 83px;
                    background: url(../img/points.svg) no-repeat center center;
                    position: absolute;
                    top: -104px;
                    left: 50%;
                    margin-left: -2.5px;
                }
            }
            .info-text {
                display: flex;
                flex-direction: column;
                align-items: center;
                h3 {
                    text-align: center;
                    margin-top: 3.6rem;
                    color: $Grey-600;
                }
                p {
                    text-align: center;
                    font-weight: 400;
                    font-size: 1.6rem;
                    line-height: 150%;
                    color: $Grey-400;
                    opacity: 0.8;
                    margin-bottom: 2.6rem;
                }
                ul {
                    li{
                        &:not(:last-child) {
                            margin-bottom: 2.6rem;
                        }
                    }
                }
            }
        }
    }
    .right-area {
        width: 100%;
        max-width: 80rem;
        .main-text {
            margin-bottom: 14.4rem;
           h2{
            max-width: 48.8rem;
            color: $Grey-600;
            margin-bottom: 4.9rem;
            }
            ul{
                margin-bottom: 3.2rem;
                li {
                    @include flex (row, center, space-between);
                    border-bottom: 1px solid rgba(225, 224, 231, 0.9); 
                    padding: 3.3rem 0rem;
                    &:first-child {
                        padding-top: 0;
                    }
                    .info {
                        display: flex;
                        align-items: center;
                        .txt {
                            margin-left: 4.3rem;
                            h3 {
                                color: $Grey-400;
                            }
                            p {
                                font-weight: 400;
                                font-size: 1.8rem;
                                line-height: 150%;
                                color: $Grey-300;
                            }
                        }
                    }
                }
            }
            .btn {
                position: relative;
                display: inline-block;
                font-family: 'Poppins', sans-serif;
                font-weight: 500;
                font-size: 1.6rem;
                line-height: 112%;
                letter-spacing: -0.5px;
                color: $brand-blue;
                &::after {
                    content: "";
                    position: absolute;
                    right: -1.8rem;
                    bottom: 0;
                    width: 10px;
                    height: 3px;
                    background-color: $brand-yellow;

                }
            } 
        }
        .box-card {
            width: 100%;
            height: 41rem;
            background: url(../img/bg-box-card-neon.svg) no-repeat center center;
            border-radius: 12px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding-left: 5.9rem;
            .text {
                width: 100%;
                max-width: 32.7rem;
                h2{
                    color: #FFFFFF;
                    margin-bottom: 1.1rem;
                }
                h3 {
                    color: #FFFFFF;
                    max-width: 32.7rem;
                    margin-bottom: 0.8rem;
                }
                p{
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 150%;
                    color: #FFFFFF;
                    margin-bottom: 3.1rem;
                }
                .btns {
                    display: flex;
                    align-items: center;
                    button {
                        &:not(:first-child) {
                        margin-left: 1.6rem;
                        }
                    }
                }
            }
            .image {
                margin-right: -3.1rem;
            }
        }
    }
}

.s-depoimentos {
    padding-top: 13.4rem;
    .top {
        @include flex (row, flex-end, space-between);
        margin-bottom: 4rem;
        h2 {
            span {
                font-family: 'Poppins', sans-serif;
                display: block; 
                font-weight: 700;
                font-size: 40px;
                line-height: 112%;
                letter-spacing: -1px;
                color: $Grey-200;
                margin-bottom: 1.4rem;
            }
            
        }
        .swiper-pagination {
            display: flex;
            position: relative;
            width: auto;
            .swiper-pagination-bullet {
                background: $brand-yellow;
                opacity: 0.3;
                border-radius: 12px;
                transition: all .3s;
                &:not(:first-child) {
                    margin-left: 0.4rem;
                }
                &-active {
                    opacity: 1;
                    width: 17.23px;
                }
            }
        }
    
    }
    .slide-depoimentos {
        overflow: hidden;
    }
}

.s-conta-digital {
    padding-top: 12.7rem;
    padding-bottom: 8.5rem;
    .container {
        display: flex;
        align-items: center;
        .text {
            max-width: 54.2rem;
            h2 {
                color: $Grey-600;
                margin-bottom: 4.5rem;
                span {
                    display: block;
                    font-family: 'Poppins', sans-serif;
                    font-weight: 600;
                    font-size: 40px;
                    line-height: 112%;
                    letter-spacing: -1px;
                    color: rgba(198, 197, 206, 0.8);
                    margin-bottom: 0.9rem;
                }
            }
            ul {
                margin-bottom: 6.1rem;
                li {
                    display: flex;
                    align-items: center;
                   &:not(:last-child) {
                    margin-bottom: 4.5rem;
                   }
                   .icon {
                        width: 4.5rem;
                        height: 4.5rem;
                        background-color: $brand-green-light;
                        border-radius: 8px;
                        @include flex (row,center,center);
                        margin-right: 2.6rem;
                   }
                   .info {
                        h4 {
                            margin-bottom: 0.4rem;
                            color: $Grey-400;
                        }
                        p {
                            font-size: 1.8rem;
                            line-height: 150%;
                            color: $Grey-300;          
                        }
                    }
                }
            }
        }
        .image {
            position: relative;
            width: 100%;
            max-width: 712px;
            height: 59.8rem;
            img {
                position: absolute;
                .circle {
                    right: 0;
                    bottom: 0;
                }
            }
        }
    }   
}







    